define(['./qs'],function(qs){
  var parse = qs.parse;
  var stringify = qs.stringify;

  return function handleQs(url, query) {
    url = url.split('?');
    var start = url[0];
    var qs = (url[1] || '').split('#')[0];
    var end = url[1] && url[1].split('#').length > 1 ? '#' + url[1].split('#')[1] : '';

    var baseQs = parse(qs);
    for (var i in query) {
      baseQs[i] = query[i];
    }
    qs = stringify(baseQs);
    if (qs !== '') {
      qs = '?' + qs;
    }
    return start + qs + end;
  }
});